<template>
  <el-row v-loading="loading" :gutter="20" class="row">
    <el-col :span="6" v-for="(item, name) in items" :key="name">
      <div class="icon">
        <span :style="{ backgroundColor: item.bg }"><img :src="require('../assets/icons/' + item.icon)"/></span>
        <div>
          <span>{{ values[name] }}</span>
          <span>{{ item.title }}</span>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import Fetch from '../utils/fetch'

const data = {
  today_invest_sum: {
    icon: 'chongzhi.svg',
    title: '今日充值',
    bg: '#8FCCF1',
  },
  yday_invest_sum: {
    icon: 'chongzhi.svg',
    title: '昨日充值',
    bg: '#5489C5',
  },
  today_cost_sum: {
    icon: 'tixian.svg',
    title: '今日提现',
    bg: '#B5D39D',
  },
  yday_const_sum: {
    icon: 'tixian.svg',
    title: '昨日提现',
    bg: '#94CAC9',
  },

  user_num: {
    icon: 'users.svg',
    title: '总会员人数',
    bg: '',
  },
  today_reg_num: {
    icon: 'users.svg',
    title: '今日注册人数',
    bg: '#D97077',
  },
  yday_reg_num: {
    icon: 'list.svg',
    title: '昨日注册人数',
    bg: '#CEC0A8',
  },
  today_login_num: {
    icon: 'edit.svg',
    title: '今日签到人数',
    bg: '#9FA9EA',
  },

  all_invest_sum: {
    icon: 'chongzhi.svg',
    title: '总充值',
    bg: '',
  },
  all_cost_sum: {
    icon: 'tixian.svg',
    title: '总提现',
    bg: '#9FA9EA',
  },
}

export default {
  data() {
    const keys = {}
    for (const key in data) {
      if (data[key]) {
        keys[key] = ''
      }
    }
    return {
      invest_sound: '',
      values: keys,
      items: data,
      loading: false,
    }
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/index')
      .then(r => {
        this.loading = false
        for (const key in this.values) {
          if (r.data[key] !== undefined) {
            this.values[key] = r.data[key]
          }
        }
      })
      .catch(() => (this.loading = false))
  },
}
</script>
