.row {
  > div {
    margin-top: 25px;
  }
}

.icon {
  display: flex;
  height: 90px;
  border-radius: 20px;
  background-color: #f3f3f3;
  align-items: center;
  overflow: hidden;

  > span {
    width: 30px;
    font-size: 40px;
    background-color: #4b9e95;
    padding: 15px 20px;
    color: #fff;
  }

  > div {
    padding: 0 20px;
    display: flex;
    line-height: 1.2;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    flex: 1;

    > span {
      font-size: 16px;

      &:first-child {
        font-size: 24px;
      }
    }
  }
}
